* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;}

/* RESTO APP  */
.restoApp {
  border: 10px groove brown;
  background-color: burlywood;
  padding: 5px;
  width: 100%;
  margin-bottom: 10px;
  text-align: center;
}
.restoTable {
  display: flex;
  flex-wrap: nowrap;
  width: fit-content;
  background-color: lightslategray;
  border: 10px groove brown;
  margin: 10px;
  padding: 10px;
}
.selector {
  padding: 3px;
  background-color: yellowgreen;
  border-radius: 10px;
  margin: 0 auto;
  margin-bottom: 5px;
}
.itemRow {
  width: 30%;
}
.itemData {
  width: 100%;
  border: 5px solid black;
  padding: 5px;
  margin: 5px;
  background-color: lightskyblue;
  text-align: center;
}
.menuSet {
  display: flex;
  justify-content: center;
}
.inputQty {
  width: 100%;
}
.addBtn {
  background-color: gold;
  border-radius: 5px;
  padding: 1px;
}
.cartName {
  width: 50%;
  border: 3px ridge chocolate;
  padding: 5px;
  margin: 0 auto;
  background-color: burlywood;
  text-align: center;
}
.cartBox {
  margin: 0 auto;
  border: 3px ridge chocolate;
  width: 50%;
  padding: 20px;
  color: white;
  background-color: black;
  text-align: end;
}
.clearBtn {
  background-color: chocolate;
  padding: 3px;
  border-radius: 10px;
  margin-bottom: 10px;
}
.cartMenu {
  display: flex;
  justify-content: space-evenly;
}
.itemName h2 {
  color: lightseagreen;
  padding: 5px;
}
.delBtn {
  background-color: lightgray;
  border: 2px red solid;
  padding: 2px;
  margin: 5px;
}
.addQty {
  padding: 2px;
  margin: 4px;
  background-color: yellowgreen;
}
.subQty {
  padding: 2px;
  margin: 5px;
  background-color: yellowgreen;
}
.price {
  margin: 5px;
}
.totalAmt {
  color:yellow;
}
.editBtn {
  background-color: green;
  padding: 1px;
}
.menuBtn {
  background-color: green;
  padding: 1px;
  margin: 2px;
}
.delMenu {
  background-color: red;
  margin-left: 1px;
  padding: 1px;
  padding-left: 4px;
  padding-right: 4px;
}
.addMenuModal {
  width: 100%;
  padding: 2px;
  margin: 2px;
}
.modalHeader {
  background-color: lightseagreen;
}
.modalBody {
  background-color: lightslategray;
  margin: 1px;
}
.modalFooter {
  background-color: lightseagreen;
}
.modalForm {
  margin: 3px;
}
.updateBtn, .addBtn {
  margin: 2px;
}
.user {
  text-align: start;
  border: 2px black solid;
  width: fit-content;
  padding: 5px;
  margin: 5px;
  background-color: lightpink;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
